/* JSHint configurations */
/* jshint esversion: 6 */
/* jshint -W097 */

'use strict';

import ChangeListener from '../../core/listener/change-listener.js';
import SVGAreaConstants from "../svg-area-constants";
import ChangeListenerConstants from "../../core/listener/change-listener-constants";

export default class FontChangeListener extends ChangeListener {

    constructor() {
        super(ChangeListenerConstants.STYLING);
    }

    /**
     * Change the font style for the specified text.
     * @param {Text} target The text that must have its font style changed.
     */
    update(target) {
        target.getTag(SVGAreaConstants.DRAWN).setAttribute("font-family", target.fontStyle.family);
        target.getTag(SVGAreaConstants.DRAWN).setAttribute("font-size", target.fontStyle.size);
        target.getTag(SVGAreaConstants.DRAWN).setAttribute("font-weight", target.fontStyle.weight);
        target.getTag(SVGAreaConstants.DRAWN).setAttribute("font-style", target.fontStyle.style);
    }
}


